
import XDialog from 'vux/src/components/x-dialog/index.vue'


export default {
  components: {
    XDialog
  },
  props: {
    show: Boolean,
    title: String,
    iconName: String,
    iconStyle: Object,
    buttonText: {
      type: String,
      default: 'OK'
    },
    maskTransition: {
      type: String,
      default: 'vux-fade'
    },
    dialogTransition: {
      type: String,
      default: 'vux-dialog'
    }
  },
  computed: {
    fullIconName () {
      return 'icon' + this.iconName
    }
  },
  methods: {
    onHide () {
      this.show = false
    }
  }
}



